.loading {
  overflow: hidden;
  transition: 0.25s height;
  background: rgb(233, 232, 232);
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  &.false {
    height: 0px;
  }
  &.true {
    height: 30px;
    span::after {
      animation: 1s animatedElipsis linear infinite;
      content: "";
      position: absolute;
    }
  }
}

@keyframes animatedElipsis {
  0% {
    content: "";
  }
  25% {
    content: " .";
  }
  50% {
    content: " . .";
  }
  75% {
    content: " . . .";
  }
}
