/* UNIVERSAL */
img,.searchButton{height:15px}
li{list-style:none;position:relative;overflow:hidden;white-space:nowrap;padding:1% 2%}
li input,li select{width:100%}
ul{margin:0 1%;padding:0 0.5%;border-radius:5px}
.macro input{position:absolute;left:0;text-align:center;width:100%}
select{border-radius:5px;border:0.5px solid lightgray;background:white;z-index:-1}
/* HEADER */
.header,.notSignedInHeader{background:linear-gradient(rgb(226, 123, 72),rgb(221, 103, 44));height:50px;min-height:50px;width:100%;top:0;overflow:show;display:flex;flex-direction:row;justify-content:space-between}
/* .notSignedInHeader{grid-template-columns:2fr 1fr} */
.header input,.header button{border-radius:5px}
/* .notSignedInHeader input[type='text'],.notSignedInHeader input[type='password']{width:40%;margin:1%;border-radius:5px;text-transform:none}
.notSignedInHeader input[type='submit'],.notSignedInHeader button{margin:1%;border-radius:5px;background:white} */
.errorMessage{color:red;font-weight:bold;text-align:center;background:white;border-radius:5px}
/* .login .errorMessage{background:white;border:1px solid black;border-radius:5px;padding:10px;position:absolute;z-index:1;margin:auto;top:75px} */
.signupForm{text-align:left;}
.signupForm .errorMessage{position:relative;z-index:0;display:block}
.signupForm input{text-transform:none;width:96%}
div.login{padding:0px;text-align:right}
.authModalBackground{z-index:1;position:fixed;background:rgba(0, 0, 0, 0.5);top:0px;left:0px;width:100%;height:100%}
/* button.authModalButton{position:relative;background:white;border-radius:25px;height:25px;width:25px} */
div.login .loginSignupButton{font-size:15px;background:white;border-radius:10px;margin:10px 5px}
.authModal{top:5%;max-width:500px;position:relative;margin:0px auto;background:linear-gradient(rgb(226, 123, 72),rgb(221, 103, 44));border-radius:5px;overflow:hidden}
.closeAndSwitchButtons{display:grid;grid-template-columns:repeat(2,5fr)1fr;}
.closeAndSwitchButtons button{background:rgba(255, 255, 255, 0.6);border:0.5px solid white;padding:5px}
form.login{margin-top:10%}
/* form.login{display:grid;grid-template-rows:repeat(3,1fr);grid-template-columns:1fr 1fr;grid-auto-flow:column;max-width:500px;position:relative;margin:0px auto;background:linear-gradient(rgb(226, 123, 72),rgb(221, 103, 44))} */
.authModal form input,.authModal form button,.toggleAuthButton{font-size:15px;padding:5px 5px;border-radius:5px;background:white;border:0.5px solid lightgray;display:block;margin:3px auto}
.authModal form input:not([type='submit']){text-transform:none}
.authModal form div{font-size:12px;cursor:pointer;padding:5px;grid-column:span 2;text-align:center;color:white}
.notSignedInMessage{padding-left:5%;color:white;}
.login{padding-top:5px}
.loginOrSignup{display:grid;grid-template-columns:repeat(2,100%);transition:0.5s}
.loginOrSignup h1{text-align:center;color:white;text-shadow:1 px 1 px 0px black,-1 px -1 px 0px black, 1 px -1 px 0 black, -1 px 1 px 0 black}
.showSignup{transform:translateX(-100%);transition:0.5s}

.welcome{padding-left:1%}

div.appName{order:1;flex:1 1;display:flex;justify-content:flex-end;align-items:center}
h1.appName{color:white;text-align:right;margin-right:15px}

div.menu{order:0;flex: 1 1;display:flex;justify-content:flex-start;align-items:center}
div.menuButton,.menuCheckbox{position:absolute;height:45px;width:45px;align-self:center;margin-left:15px}
.menuCheckbox{opacity:0;z-index:1}
div.menuButton span{background:white;height:15%;margin:15% 0;width:100%;position:relative;display:block;border-radius:25px;transition:0.5s}
.menuCheckbox:checked~.menuButton span{transition:0.5s}
.menuCheckbox:checked~.menuButton span:first-child{transform:rotate(-33deg)translateX(-10px)scaleX(0.75)}
.menuCheckbox:checked~.menuButton span:nth-child(2){transform:translateX(5px)}
.menuCheckbox:checked~.menuButton span:last-child{transform:rotate(33deg)translateX(-10px)scaleX(0.75)}
.sideMenu{position:fixed;background:white;top:50px;bottom:50px;left:-350px;z-index:2;margin:0;border-radius:0px;border-right:5px solid rgb(221, 103, 44);width:33%;min-width:225px;max-width:300px;transition:0.375s;padding:5% 1%;}
.menuCheckbox:checked~.sideMenu{left:0px;transition:0.375s}

.sideMenu ul{width:100%;margin:0px 2% 0 0;padding:0 2% 0 0}
.sideMenu ul li,.sideMenu h1{padding:5% 2%;margin:0 2% 0 0;width:100%;transition:0.25s}
.sideMenu ul li{cursor:pointer;}
.sideMenu ul li:hover{transition:0.5s;text-decoration:underline}
.contact a{text-decoration:none;color:black}
div.daySelect{order:1;flex: 2 2;display:flex;justify-content:center;align-items:center}
span.daySelect{position:absolute;width:300px;max-width:38%;display:block;margin:auto}
.daySelect select{background:white;width:100%}
/* .daySelect .dropdown button.nonMenu{padding:0;width:15%;color: rgb(245, 245, 245);background:transparent;border:none;font-weight:bold;font-size:1em} */
.copy{font-size:18px;position:relative;right:19%}
.daySelect input[type='text']{text-align:center;width:calc(100%-25px)}
.daySelect input[type='date']{width:50%;background:white;min-width:auto}
.daySelect input[type='submit']{position:relative;padding:1% 10%;margin-left:9%;color:white;background:none;border-radius:5px}
.daySelect form{margin-top:5%}
