/* PASSWORD RESET */
.password-reset-background{background:rgba(0, 0, 0, 0.5);position:fixed;z-index:1;height:100%;width:100%;left:0px;top:0px;}
.password-reset{background:white;position:relative;width:33%;min-width:300px;margin:auto;top:10%;z-index:1;border-radius:5px;border: 1px solid black;}
.password-reset input{border-radius:3px;border:1px solid lightgray;padding:5px}
.password-reset input[type="submit"],.password-reset button{border-radius:3px;border:1px solid black;padding:5px;margin:5px;min-width:25px}
/* .password-reset h2{margin-top:30px} */

