/* SIGNUP */
.modal{background:white;position:fixed;z-index:1;margin-left:-20%;width:60%;max-width:500px;border-radius:5px;border: 1px solid black;right:0%}
.modal form{position:relative;width:100%;padding:2%}
.modal input{width:95%;position:relative;border:0.5px solid lightgray;border-radius:5px;padding:2%}
/* SIGNUP */
/* CREATE FOOD */
.createFoodContainer button{background:white;position:relative;float:right;border-radius:5px;border:1px solid black;padding:1%;z-index:1;margin:5px}
.createFoodContainer{visibility:visible;background:rgb(221, 103, 44);position:fixed;top:0%;width:100%;z-index:1;height:100%;overflow:scroll;right:0}
.createFoodContainer form{position:relative;width:95%;display:block;margin-top:50px}
.createFoodContainer .newFoodGrid li{padding:10px 10px;margin:0px}
.createFoodContainer label{color:white;padding:0;margin:0}
.createFoodContainer label:after{content:': '}
.createFoodContainer input,.createFoodContainer select{width:50%;position:absolute;right:1%;border-radius:5px;padding:0;margin:0x}
.createFoodContainer select{z-index:2}
.createFoodContainer input[type='submit']{background:white;position:relative;padding:1%;border:1px solid black;width:50;display:block;margin:auto}
div.showMore{border:1px solid black;border-radius:5px;padding:1%;color:white;margin-left:12.5%;display:inline-block;width:75%;text-align:center}
/* CREATE FOOD */
/* GOALS */
.goals{margin-top: 10px;}
.goals input{position:absolute;width:90%;text-align:center;box-shadow: 1px 1px 1px lightgray;border-radius:5px;height:70%;padding:0}
input.submitGoal{position:relative;height:100%;padding:0px;background:rgb(221, 103, 44);color:white}
.autogenerateMacros{position:fixed;background:white;z-index:1;top:10%;bottom:10%;width:100%}
.autogenerateMacros form{margin:2%}
.autogenerateMacros div{position:relative;margin:5px 0}
.autogenerateMacros label.gender{width:100%;text-align:center}
.autogenerateMacros .gender label{width:15%}
.autogenerateMacros .gender input{width:65%}
.autogenerateMacros select{max-width:40%}
.autogenerateMacros label{position:relative;width:30%;display:inline-block;}
.autogenerateMacros input{position:relative;width:33%}
.autogenerateMacros button{position:absolute;z-index:1;top:1%;right:1%;border:1px solid black;border-radius:5px;background:rgb(221, 103, 44);color:white}
.autogenerateMacros input[type='submit']{width:100%;margin-top:1%;border:1px solid black;border-radius:5px;background:rgb(221, 103, 44);color:white}
label.macrosForAutogenerate{width:auto;margin:0% 1% 0% 2%}
input.macrosForAutogenerate{max-width: 5%}
/* GOALS */
/* TABLE */
.table{position:absolute;top:50px;bottom:90px;width:100%;overflow-y:hidden;overflow-x:hidden;font-size:.9em}
.categoryScrollContainer{position:relative;height:85%;width:100%;overflow-y:scroll;overflow-x:hidden;}
.grid{display:grid;grid-template-columns: 3fr 1fr 2fr  repeat(4, 1fr) 3%}
.goals .goalsSelect{grid-column:span 2}
.goals input{position:absolute;width:100%}
input.readonly{background:rgb(226, 226, 226)}
.key{text-align:center; font-size: 0.5em}
.totals{grid-column:span 3;text-align:right}
/* .under{border-bottom:2px solid yellow} */
.over{border-bottom:2px solid red}
.good{border-bottom:2px solid green}
.amount input{position:absolute;width:90%;text-align:center;box-shadow: 1px 1px 1px lightgray;border-radius:5px;height:70%;padding:0}
.macro{text-align:center;padding: 4% 0 0 0}
.saveButton{visibility:hidden}
/* TABLE */

/* CATEGORY */
.category{background:rgb(255, 204, 178);border-radius:5px;margin:1% 0;padding:1% 0}
.categoryName{position:relative;margin:4px;left:10%;width:80%;}
.subtotals{grid-column:span 2}
.choice{background:white;border-top:1px solid rgba(194, 194, 194, 0.589);border-bottom:1px solid rgba(194, 194, 194, 0.589);transition:0.25s;overflow:hidden;height:20px}
.toExpand{transform: scaleX(1.01);background:gold;border-top:1px solid rgba(194, 194, 194, 0.589);border-bottom:1px solid rgba(194, 194, 194, 0.589);transition:0.25s;overflow:hidden;height:20px;}
.shrunk{background:red;border-top:0px solid rgba(194, 194, 194, 0.589);border-bottom:0px solid rgba(194, 194, 194, 0.589);overflow:hidden;height:0px; transition:0.5s}
.choice:hover {transform: scaleX(1.01);transition:0.25s;box-shadow: 0px 1px 5px rgb(90, 90, 90)}

.deleteColumn{text-align:center;overflow:visible}
button.deleteButton{border:none;height:100%;background: none;}
.choice button.deleteButton{padding:1px 0;border:none;border-radius:25px;transition:0.25s}
.choice button.deleteButton span{display:block;margin:1px}
.choice:hover button.deleteButton{background:rgb(255, 90, 90);transition:0.25s;margin:1px}
.choice:hover button.deleteButton span{margin:-8px 0px;}
/* .choice:hover button.deleteButton{visibility:visible;} */
/* img.deleteChoice{position:relative;background:none;border-radius:0px;padding:10%;transition:0.25s;right:0} */
/* .choice:hover img.deleteChoice{background:rgb(255, 90, 90);border-radius:25px;border:0.25px solid black;transition:0.25s;padding:5%} */

.choice li.name:hover span.nameScroll{position:fixed;z-index:1;background:white;}

/* .arrow{position:fixed;bottom:50%;margin-left:20%;padding:2%;border-radius:5px;border:1px solid black;animation:sway 1.3s linear infinite;}*/
div.arrow{position:fixed;bottom:20%;border:1px solid black;border-radius:5px;padding:1%;background:white;color:black;left:15%;animation:sway 1.3s infinite}
.arrow:after{content:'';position:absolute;border-width:25px;border-style:solid;border-color:black transparent transparent transparent;right:110%}


@keyframes sway {
  0% { bottom: 18%; }
  50% { bottom: 19%; }
  100% { bottom: 18%; }
}
