/* FOOTER SEARCH */
.search {
  position: absolute;
  bottom: 55px;
  width: 100%;
}
.search input.searchText {
  display: inline-block;
  font-size: 1em;
}
.search .foodSearchContainer {
  border: 1px solid rgb(221, 103, 44);
  border-radius: 25px;
  background: white;
}
.search .searchForm {
  margin: 0;
  padding: 0 10px;
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  border-radius: 25px;
}
.search .searchText {
  flex: 100% 0;
  position: relative;
  padding: 10px 0;
  border-radius: 25px;
  border: 0px solid transparent;
}
.searchForm .searchButton {
  flex: 35px 0;
  height: 35px;
  background: white;
}
.loadMore {
  background: lightgray;
  text-align: center;
  font-weight: bold;
}

/* SEARCH */
.searchForm{text-align:center;margin-left:2%}
.searchForm input[type='text']{display:none}
.searchForm button{display:none}
.foodForm{display:none}
.category .foodSearchContainer{display: none}
/* SEARCH */
/* SEARCH RESULT */
.searchResultContainer,.searchResultContainerHidden{display:none;background:white;position:absolute;z-index:1;border-radius:0px;width:99%;padding:0px;margin:0;border:1px solid gray;position:absolute;bottom:100%;max-height: 800%;overflow-y:scroll;box-shadow:inset 2px 2px 2px gray;}
@keyframes slideUpShow{from{height:0px}to{height:800%}}
.foodSearchContainer:hover .searchResultContainer{display:block; animation:slideUpShow 0.25s}
.searchResultContainer h5,.searchResultContainerHidden h5{text-align:center;margin:2%;background:rgb(221, 103, 44);color:white}
.searchResult{border-bottom:0.5px solid lightgray;cursor:pointer}
.searchResult:hover{transform:scale(1.01);transition:0.25s;box-shadow: 0px 1px 5px rgb(90, 90, 90)}
ul.resultMacros{font-size:0.5em;display:grid;grid-template-columns:repeat(3,1fr);text-align:left;padding-left:20%}
.resultName{font-size:0.7em;}
.brandName{font-size:0.5em}
.resultMacros{display:block;text-align:right;font-size:0.7em}
div.searchLoading{height:30px;width:100%;background:lightgray;position:relative;box-shadow:inset 3px 0px 3px darkgray}
div.searchLoading span{background:none;position:absolute;border: 10px solid #f3f3f3;border-top: 10px solid rgb(221, 103, 44);border-bottom: 10px solid rgb(221, 103, 44);border-radius: 50%;width: 5px;height: 5px;animation: spin 1.5s linear infinite;left:40%;top:2px}
/* SEARCH RESULT */
/* DROPDOWN */

.daySelect .dropdown{right:-20px}
.goals .dropdown{right:0px}
/* .dropdown{padding:0;background:white;position:absolute;display:inline-block;border:1px solid lightgray;border-radius:5px;white-space:nowrap;min-width:20px;text-align:right;overflow:auto;z-index:1} */
.dropdown{display:inline-block;position:absolute;text-align:right;z-index:1}
.dropdown .dots{padding:1px;background:white;width:15px;height:15px;display:inline-block;transition:transform 0.25s;margin-right:2.5px;margin-top:1px;border-radius:25px;border:1px solid lightgray}
.dropdown .dots span{display:inline-block;background:black;height:3px;width:3px;border-radius:25px;margin:1px;margin-bottom:5px}
.goals .dropdown .dots span{margin-bottom:4px}
.dropdown:hover .dots{transform:rotate(90deg);transition:transform 0.25s}

.dropdown-content{display:none}
.dropdown:hover .dropdown-content{position: absolute;padding:0px;margin:0px;text-align:left;display:block;border:1px solid black;border-radius:5px;overflow:hidden;right:calc(100% - 20px);}
.dropdown-content li{background:white;padding:15px 10px;margin:0;border-top:1px solid lightgray;cursor:pointer;font-size:18px}
.dropdown-content li:hover{background:rgb(240, 240, 240)}
.dropdown-content li button{background:none;border:none;cursor:pointer}

span.icon{height:15px;width:15px;display:block;background-size:100%;padding:0px;margin:0 4px 0 -3px;background-repeat:no-repeat;background-position:center}
span.addIcon{background-image:url("./icons/add-icon-circle.png");}
span.editIcon{background-image:url("./icons/edit-icon.png");}
span.deleteIcon{background-image:url("./icons/trash-icon.png");background-size:80%}
span.autogenerateIcon{
  background: url('./icons/autogenerate-icon.png');
  background-size: 100%;
}


/* NOT CELL PHONES */
@media only screen and (min-width: 768px) {
.table{left:29%;width:70%;bottom:50px}
.autogenerateMacros{width:70%}
.autogenerateMacros select{max-width:50%}
select.daySelect{max-width:300px}
.daySelect input[type='text']{width:50%}
.daySelect input[type='date']{width:25%}
.daySelect input[type='submit']{position:relative;padding:1% 9%;margin-left:0%;color:white;background:none;border-radius:5px}

.search .foodSearchContainer{position:fixed;top:55px;left:0%;width:28%;padding:5px 0}
.searchText{font-size:0.5em}
.searchResultContainer,.searchResultContainerHidden{display:block;position:fixed;top:105px;bottom:50px;width:28%;animation:none}
.arrow{background:white;position:fixed;top:4%;left:-15%;padding:1%;height:2%;border-radius:5px;border:1px solid black;animation:sway 1s linear infinite;}
/* .arrow:after{content:'';position:absolute;border-width:10px;border-style:solid;border-color:black transparent transparent transparent;right:-18%;top:75%} */
@keyframes sway {
  0% { top: 4%; }
  50% { top: 5.5%; }
  100% { top: 4%; }
}
.createFoodContainer{left:0px;width:50%;border-right:5px solid white}
.createFoodContainer ul li{padding:1%}
.foodSearchContainer:hover .searchResultContainer{animation:none}
}

/* ONLY DESKTOP */
@media only screen and (min-width: 992px) {
.choice .deleteButton{visibility:hidden;opacity:0}
.choice:hover .deleteButton{visibility:visible;opacity:1;}
} 

@media only screen and (max-height: 550px){
  .table{top:60px;bottom:40px;padding-bottom:20px;}
  .search{bottom:0px}
}
